<script>
import Web3 from 'web3';
if (typeof Web3 !== 'undefined') {

  let  web3 = new Web3(new Web3.providers.HttpProvider('https://ropsten.infura.io/v3/829a14a4e6a5475797232b2333cface6')); // ropsten 네트워크로 연결
// web3 = new Web3(new Web3.providers.HttpProvider('https://ropsten.infura.io')); // ropsten 네트워크로 연결
   // web3 = new Web3(Web3.currentProvider);
  console.log('Web3 -> Current Provider');
} else {
  // set the provider you want from Web3.providers
    let web3 = new Web3(new Web3.providers.HttpProvider('https://ropsten.infura.io')); // ropsten 네트워크로 연결
}
export default {


}
