<template>
<div>
  <div v-show="showAdminDepoDisplay===0">
    <div id="spinner" class="spinner-border text-success" role="status">
      <span class="sr-only">Loading...</span>
    </div>
  </div>

  <div class="container-fluid">
    <div id="row1" class="row">
      <div class="col align-self-center">
        <img src="@/assets/logo.png">
      </div>
      <div class="col align-self-center">
        <b-link id="home1" to="/admin/home">Home</b-link>
      </div>
      <div class="col align-self-center">
        <b-link id="exchange1" to="/admin/deposit">입금</b-link>
      </div>
      <div class="col align-self-center">
        <b-link id="send1" to="/admin/withdraw">출금</b-link>
      </div>
      <div class="col align-self-center">
        <b-link id="transaction1" to="/admin/balance">잔액 조회</b-link>
      </div>
    </div>

    <table id="table_depo" class="table" v-show="showAdminDepoDisplay===1">
      <thead>
        <tr>
          <th colspan="2" scope="colgroup">
            Token Exchange Contract
          </th>
        </tr>
        <tr>
          <th scope="col">Token Name</th>
          <th scope="col">Token Balance</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="key in param">
          <td>
            {{ key.tokenName }}
          </td>
          <td>
            {{key.tokenBalance}}
          </td>
        </tr>
      </tbody>
    </table>

    <div class="row" v-show="showAdminDepoDisplay===1">
      <form autocomplete="off">
      <span id="depo_account">보낼 지갑 주소</span>
      <span id="depo_tokenAmout">토큰 종류, 양</span>
      <input id="input_depo" type="text" class="form-control" v-model="fromAccountAddress" placeholder="계좌 주소를 입력하세요">
      <input id="num_amount1" type="text" class="form-control" v-model="amountNum" placeholder="토큰의 양 입력">
      </form>
      <select id="token_select" class="form-control" v-model="tokenSelected">
        <option value="A_token">A_token</option>
        <option value="B_token">B_token</option>
        <option value="C_token">C_token</option>
      </select>
      <button type="button" class="btn btn-success btn-lg" v-on:click="next">입금하기</button>
    </div>

    <table id="table_depo2" class="table" v-show="showAdminDepoDisplay===2">
      <thead>
        <tr>
          <th scope="col">정보</th>
          <th scope="col">값</th>
        </tr>
      </thead>

      <tbody>
        <tr>
          <td>
            보내는 사람 계좌 주소
          </td>
          <td>
            {{param3.from_account}}
          </td>
        </tr>
        <tr>
          <td>
            토큰 종류
          </td>
          <td>
            {{param3.tokenName}}
          </td>
        </tr>
        <tr>
          <td>
            토큰 양
          </td>
          <td>
            {{param3.input_num}}
          </td>
        </tr>


      </tbody>

    </table>

    <table id="table_depo3" class="table" v-show="showAdminDepoDisplay===2">
      <thead>
        <tr>
          <th colspan="2" scope="colgroup">
            Token Exchange Contract
          </th>
        </tr>
        <tr>
          <th scope="col">Token Name</th>
          <th scope="col">Token Balance</th>
        </tr>
      </thead>

      <tbody>
        <tr v-for="key in param">
          <td>
            {{ key.tokenName }}
          </td>
          <td>
            {{key.tokenBalance}}
          </td>
        </tr>

      </tbody>

    </table>

  </div>

  <img id="depo_arrow_show" src="../assets/arrow_show.png" v-show="showAdminDepoDisplay===2">
  <img id="depo_back_button" src="../assets/before_button.png" v-on:click="back" v-show="showAdminDepoDisplay===2">
  <img id="depo_next_button" src="../assets/next_button.png" v-on:click="next2" v-show="showAdminDepoDisplay===2">
</div>
</template>

<script>
import Web3 from 'web3';
import store from '../store';

import {
  Admin_account_address,
  TokenExchange_address,
  TokenExchange_ABI,
  ERC20_ABI,
  A_tokenAddress,
  B_tokenAddress,
  C_tokenAddress,
  Token_ABI
} from './Contract'

let tokens = [A_tokenAddress, B_tokenAddress, C_tokenAddress];

if (typeof web3 !== 'undefined') {
  // Mist/MetaMask의 프로바이더 사용
  web3 = new Web3(web3.currentProvider);
  // alert('web3가 주입되었습니다.');
} else {
  // 사용자가 Metamask를 설치하지 않은 경우에 대해 처리
  // 사용자들에게 Metamask를 설치하라는 등의 메세지를 보여줄 것
  web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));
}

let walletAddress;
let adminWalletAddress;
let adminAccountInfo;
let checkToken = false;
let depoAvailable = false;
let tokenIndex;

let accounts;

export default {

  data() {
    return {
      param: '',
      fromAccountAddress: '',
      amountNum: '',
      tokenSelected: "A_token"
    }
  },

  created: function() {
    store.commit('adminDepoInitialization');
    store.commit('adminAccountInfoInitialization');
  },

  computed: {
    showAdminDepoDisplay: function() {
     return this.$store.state.adminDepoShowDisplay;
   },

    param3: function() {
      return this.$store.state.depo;
    }
  },

  methods: {
    next: function(event) {

      if (this.fromAccountAddress && this.amountNum)
      {
        let checkFromAccountAddress = this.fromAccountAddress.substring(0, 2);

        if (checkFromAccountAddress != "0x") alert("올바른 계좌 주소 형식을 입력해주세요.")
        else if (this.fromAccountAddress.length != 42) {
          alert("올바른 계좌 주소 형식을 입력해주세요.")
        } else {
          function checkMetaMaskAccount(callback) {
            return new Promise((resolve, reject) => {
              web3.eth.getAccounts().then(res => {
                if(res)
                {
                  accounts = res;
                  resolve()
                }
                else {
                  console.error("오류내용 : 메타마스크의 연동된 계좌 정보를 가져오지 못했습니다 (내부 실행 오류)");
                  alert("오류내용 : 메타마스크의 연동된 계좌 정보를 가져오지 못했습니다 (내부 실행 오류)");
                  reject()
                }
              });
            });
          }

          checkMetaMaskAccount().then(()=> {

          if ("A_token" == this.tokenSelected) {

            store.commit('pollDepoInfo', {
              from_account: this.fromAccountAddress,
              tokenName: "A_token",
              tokenSelected: this.tokenSelected,
              input_num: this.amountNum
            });

            store.commit('changeAdminDepoDisplay', 2);
          }

          if ("B_token" == this.tokenSelected) {

            store.commit('pollDepoInfo', {
              from_account: this.fromAccountAddress,
              tokenName: "B_token",
              tokenSelected: this.tokenSelected,
              input_num: this.amountNum
            });

            store.commit('changeAdminDepoDisplay', 2);

          }

          if ("C_token" == this.tokenSelected) {

            store.commit('pollDepoInfo', {
              from_account: this.fromAccountAddress,
              tokenName: "C_token",
              tokenSelected: this.tokenSelected,
              input_num: this.amountNum
            });

            store.commit('changeAdminDepoDisplay', 2);

          }

          this.checkAdminAccountBal();
        });
        }
      }
      else
      {
        alert("정보를 모두 입력하세요")
      }

    },

    next2: function(event) {
      if(this.fromAccountAddress != accounts[0])
      {
        console.log("입금 승인 실패 \n입력하신 계좌 주소와 현재 연동되어 있는 메타마스크 계좌 주소가 일치하지 않습니다");
        alert("입금 승인 실패 \n입력하신 계좌 주소와 현재 연동되어 있는 메타마스크 계좌 주소가 일치하지 않습니다");
      }
      else if(adminWalletAddress != Admin_account_address)
      {
        console.log('입금 승인 실패 \n현재 연동되어 있는 메타마스크 계좌가 관리자 계좌인지 확인하세요');
        alert("입금 승인 실패 \n현재 연동되어 있는 메타마스크 계좌가 관리자 계좌인지 확인하세요");
      }
      else {

      let selectedSendTokenAddress;
      let currentTokenSelected = this.$store.state.depo.tokenName;
      let currentInputNum = web3.utils.toWei(this.$store.state.depo.input_num, 'ether');

      for(let k=0; k < adminAccountInfo.length; k++)
      {
        if(adminAccountInfo[k].tokenName == "A_token")
        {
          checkToken = true;
          tokenIndex = k;
        }

        if(adminAccountInfo[k].tokenName == "B_token")
        {
          checkToken = true;
          tokenIndex = k;
        }

        if(adminAccountInfo[k].tokenName == "C_token")
        {
          checkToken = true;
          tokenIndex = k;
        }
      }

      if (currentTokenSelected === "A_token") {
        selectedSendTokenAddress = A_tokenAddress;

        if(!checkToken)
          alert("선택하신 토큰과 입력하신 양에 대하여\n관리자 계좌가 충분히 보유하고 있지 않습니다");
        else if(this.amountNum > adminAccountInfo[tokenIndex].tokenBalance)
          alert("선택하신 토큰과 입력하신 양에 대하여\n관리자 계좌가 충분히 보유하고 있지 않습니다");
        else
        {
          depoAvailable = true;
          alert("메타마스크를 통해 입금을 진행하세요");
        }
      }

      if (currentTokenSelected === "B_token") {
        selectedSendTokenAddress = B_tokenAddress;

        if(!checkToken)
          alert("선택하신 토큰과 입력하신 양에 대하여\n관리자 계좌가 충분히 보유하고 있지 않습니다");
        else if(this.amountNum > adminAccountInfo[tokenIndex].tokenBalance)
          alert("선택하신 토큰과 입력하신 양에 대하여\n관리자 계좌가 충분히 보유하고 있지 않습니다");
        else
        {
          depoAvailable = true;
          alert("메타마스크를 통해 입금을 진행하세요");
        }
      }

      if (currentTokenSelected === "C_token") {
        selectedSendTokenAddress = C_tokenAddress;

        if(!checkToken)
          alert("선택하신 토큰과 입력하신 양에 대하여\n관리자 계좌가 충분히 보유하고 있지 않습니다");
        else if(this.amountNum > adminAccountInfo[tokenIndex].tokenBalance)
          alert("선택하신 토큰과 입력하신 양에 대하여\n관리자 계좌가 충분히 보유하고 있지 않습니다");
        else
        {
          depoAvailable = true;
          alert("메타마스크를 통해 입금을 진행하세요");
        }
      }

      if(depoAvailable == true)
      {

      let tokenContract = new web3.eth.Contract(Token_ABI, selectedSendTokenAddress);
      let TokenExchangeContract = new web3.eth.Contract(TokenExchange_ABI, TokenExchange_address);

      function setApprove(callback) {
        return new Promise((resolve, reject) => {
            tokenContract.methods.increaseApprove(TokenExchange_address, currentInputNum).send({
              from: accounts[0]
            }, (error, result) => {
              if (!error) {
                resolve()
              } else {
                if (error.message.includes("User denied transaction signature")) {
                  console.log('입금 승인을 거부 하셨습니다');
                  alert("입금 승인을 거부 하셨습니다")
                  reject()
                } else {
                  console.log('입금 승인 실패 \n현재 연동되어 있는 메타마스크 계좌가 관리자 계좌인지 확인하세요');
                  alert("입금 승인 실패 \n현재 연동되어 있는 메타마스크 계좌가 관리자 계좌인지 확인하세요");
                  reject()
                }
              }
            });

        });
      }

      setApprove()
        .then(() => { //then
          alert("입금 승인이 성공하였습니다. 메타마스크를 통해 다음을 진행해주세요")

          function startDeposit(callback) {
            return new Promise((resolve, reject) => {

                TokenExchangeContract.methods.deposit(selectedSendTokenAddress, currentInputNum).send({
                  from: accounts[0]
                }, (error, result) => {
                  if (!error) {
                    console.log("입금 성공");
                    resolve()
                  } else {
                    if (error.message.includes("User denied transaction signature")) {
                      alert("입금 진행을 거부하셨습니다.")
                      reject()
                    }
                    else if (error.message.includes("rpc error with payload")) {
                      alert("입금하려고 하는 양만큼 계좌가 보유하고 있지 않습니다.\n계좌 잔액을 확인하세요.")
                      reject()
                    }
                     else {
                      alert("입금 실패 \n현재 연동되어 있는 메타마스크 계좌가 관리자 계좌인지 확인하세요");
                      reject()
                    }
                  }
                });

            }); //end of Promise

          }

          startDeposit()
            .then(function() {
              location.href = "http://localhost:8080/admin/deposit/finalconfirm";
              alert("입금이 완료되었습니다");
            })
        })
      }
    }

    },

    show: function() {

      walletAddress = TokenExchange_address;

      function getEther(callback) {
        return new Promise((resolve, reject) => {
          web3.eth.getBalance(walletAddress, (error, balance) => {
            if (!error) {
              let ether = web3.utils.fromWei(balance, 'ether'); //Push Ether : balance  to array

              store.commit('setAdminDepoInfo',{
              tokenName : "ETHER",
              tokenBalance : ether
            });
              resolve()
            } else {
              console.error("오류내용 : 계좌의 Ether 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (web3 문제 또는 잘못된 TokenExchange address 설정)");
              alert("오류내용 : 계좌의 Ether 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (web3 문제 또는 잘못된 TokenExchange address 설정)");
              reject()
            }
          }); //end of getBalance
        }); //end of Promise
      } //end of getEther

      getEther()
        .then(() => { //then search ERC20 tokens

          const getTokenBalance = new Promise((resolve, reject) => {
            let ctn = 0;

            tokens.forEach(function(token) {
              let contract = new web3.eth.Contract(ERC20_ABI, token);

              contract.methods.balanceOf(walletAddress).call({
                from: walletAddress
              }, (error, balance) => {
                contract.methods.decimals().call({
                  from: walletAddress
                }, (error, decimals) => {
                  if (!error) {

                    let calculatedResult = balance / (Math.pow(10, decimals));

                    contract.methods.name().call({
                      from: walletAddress
                    }, (error, tokenName) => {

                      ctn++;

                      if (balance.toString() != '0') {
                        store.commit('setAdminDepoInfo',{
                        tokenName : tokenName,
                        tokenBalance : calculatedResult
                      });
                      }
                      if (ctn == tokens.length) {
                        ctn = 0;
                        resolve()
                      }
                    }); //end of name
                  } else {
                    console.error("오류내용 : 계좌의 Token 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (잘못된 Token address 설정)");
                    alert("오류내용 : 계좌의 Token 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (잘못된 Token address 설정)");
                    console.log(error);
                  }
                }); //end of decimals
              }); //end of balanceof
            }); //end of tokens

          });

          getTokenBalance.then(result => {
            this.param = store.state.adminDepoInfo;
            store.commit('changeAdminDepoDisplay', 1);
          });

        });

    },

    checkAdminAccountBal: function() {

      adminWalletAddress = this.fromAccountAddress;

      function getEther(callback) {
        return new Promise((resolve, reject) => {
          web3.eth.getBalance(adminWalletAddress, (error, balance) => {
            if (!error) {
              let ether = web3.utils.fromWei(balance, 'ether'); //Push Ether : balance  to array

              store.commit('setAdminAccountInfo',{
              tokenName : "ETHER",
              tokenBalance : ether
            });
              resolve()
            } else {
              console.error("오류내용 : 계좌의 Ether 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (web3 문제 또는 잘못된 TokenExchange address 설정)");
              alert("오류내용 : 계좌의 Ether 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (web3 문제 또는 잘못된 TokenExchange address 설정)");
              reject()
            }
          }); //end of getBalance
        }); //end of Promise
      } //end of getEther

      getEther()
        .then(() => { //then search ERC20 tokens

          const getAdminTokenBalance = new Promise((resolve, reject) => {
            let ctn = 0;

            tokens.forEach(function(token) {
              let contract = new web3.eth.Contract(ERC20_ABI, token);

              contract.methods.balanceOf(adminWalletAddress).call({
                from: adminWalletAddress
              }, (error, balance) => {
                contract.methods.decimals().call({
                  from: adminWalletAddress
                }, (error, decimals) => {
                  if (!error) {

                    let calculatedResult = balance / (Math.pow(10, decimals));

                    contract.methods.name().call({
                      from: adminWalletAddress
                    }, (error, tokenName) => {

                      ctn++;

                      if (balance.toString() != '0') {
                        store.commit('setAdminAccountInfo',{
                        tokenName : tokenName,
                        tokenBalance : calculatedResult
                      });
                      }
                      if (ctn == tokens.length) {
                        ctn = 0;
                        resolve()
                      }
                    }); //end of name
                  } else {
                    console.error("오류내용 : 계좌의 Token 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (잘못된 Token address 설정)");
                    alert("오류내용 : 계좌의 Token 잔액을 가져오지 못했습니다 \n- 내부 실행 오류 (잘못된 Token address 설정)");
                    console.log(error);
                  }
                }); //end of decimals
              }); //end of balanceof
            }); //end of tokens

          });

          getAdminTokenBalance.then(result => {
            adminAccountInfo = store.state.adminAccountInfo;
            console.log("end");
          });

        });

    },

    back: function(event) {
      this.amountNum = '';
      this.fromAccountAddress = '';
      checkToken = false;
      store.commit('changeAdminDepoDisplay', 1);
      store.commit('adminDepoInitialization');
      store.commit('adminAccountInfoInitialization');

    },
  },

  mounted() {
    this.show();
  }
}
</script>

