
<template>
<div>

  <div v-show="showDisplay===0">

    <div class="container-fluid">
      <div class="row">
        <template>
          <div id="showProgress">
            <div class="container">
              <ul class="progressbar">
                <li class="active">환전 신청</li>
                <li>입금 대기</li>
                <li>입금 완료</li>
                <li>송금 진행</li>
                <li>환전 완료</li>
              </ul>
            </div>
          </div>
        </template>
      </div>

      <table id="showRate" class="table">
        <thead>
          <tr>
            <th scope="col">토큰 이름</th>
            <th scope="col">환율</th>
          </tr>
        </thead>

        <tbody>

          <tr v-for="(value,key) in calculatedShowRate">
            <td>
              {{key}}
            </td>
            <td>
              {{value}}
            </td>
          </tr>
        </tbody>

      </table>

      <div id="input_row" class="row">
        <span id="send_account">환전 지갑 주소</span>
        <input id="input_exchange" type="text" class="form-control" v-model="sendAccountAddress" placeholder="계좌 주소를 입력하세요">
        <input id="sendAmount" type="text" class="form-control" v-model="sendAmount" placeholder="토큰의 양 입력">
        <select id="send_token_select" class="form-control" v-model="sendSelected">
          <option value="A_token">A_token</option>
          <option value="B_token">B_token</option>
          <option value="C_token">C_token</option>
        </select>


        <input id="receiveAmount" type="text" class="form-control" :value="calculatedRate">
        <select id="receive_token_select" class="form-control" v-model="receiveSelected">
          <option value="A_token">A_token</option>
          <option value="B_token">B_token</option>
          <option value="C_token">C_token</option>
        </select>
      </div>
    </div>

    <img id="exchange_before_button" v-on:click="before" src="../assets/before_button.png">
    <img id="exchange_next_button" v-on:click="confirm" src="../assets/next_button.png">
  </div>
  <!-- showDisplay 0   환전 신청  -->
  <div v-show="showDisplay===1">
    <div id="showProgress">
      <div class="container">
        <ul class="progressbar">
          <li class="active">환전 신청</li>
          <li class="active">입금 대기</li>
          <li>입금 완료</li>
          <li>송금 진행</li>
          <li>환전 완료</li>
        </ul>
      </div>
    </div>
    <table class="table table-bordered">
      <thead>
        <th scope="col"> 정보</th>
        <th scope="col">값</th>
      </thead>
      <tbody>
        <tr>
          <td> 보내는 사람 주소 </td>
          <td> {{exchangeInfoParam.sendAccountAddress}} </td>
        </tr>
        <tr>
          <td> 보내는 양 </td>
          <td> {{exchangeInfoParam.sendAmount}} </td>
        </tr>
        <tr>
          <td> 받는사람 주소 </td>
          <td> {{exchangeInfoParam.sendAccountAddress}} </td>
        </tr>
        <tr>
          <td> 받는 양 ( 추정치 )</td>
          <td> {{exchangeInfoParam.receiveAmount}} </td>
        </tr>
        <tr>
          <td> 수수료 </td>
          <td> 0.001 Ether </td>
        </tr>
        <tr>
          <td> 환율 </td>
          <td> 1 {{exchangeInfoParam.sendSelected}} : {{exchangeInfoParam.rateIndex/10**8}} {{exchangeInfoParam.receiveSelected}} </td>
        </tr>
      </tbody>
    </table>

    <img id="exchange_before_button" v-on:click="before" src="../assets/before_button.png">
    <img id="exchange_next_button" v-on:click="exchange" src="../assets/next_button.png">

  </div>
  <!-- showDisplay 1  -->
  <div v-show="showDisplay===2">
    <div>
      <div id="showProgress">
        <div class="container">
          <ul class="progressbar">
            <li class="active">환전 신청</li>
            <li class="active">입금 대기</li>
            <li>입금 완료</li>
            <li>송금 진행</li>
            <li>환전 완료</li>
          </ul>
        </div>
      </div>
      <div id="spinner" class="spinner-border text-success" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <!-- showDisplay 2  입금 대기-->
  <div v-show="showDisplay===3">
    <div>
      <div id="showProgress">
        <div class="container">
          <ul class="progressbar">
            <li class="active">환전 신청</li>
            <li class="active">입금 대기</li>
            <li class="active">입금 완료</li>
            <li>송금 진행</li>
            <li>환전 완료</li>
          </ul>
        </div>
      </div>
      <div id="spinner" class="spinner-border text-success" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <!-- showDisplay 3  입금완료 -->
  <div v-show="showDisplay===4">
    <div>
      <div id="showProgress">
        <div class="container">
          <ul class="progressbar">
            <li class="active">환전 신청</li>
            <li class="active">입금 대기</li>
            <li class="active">입금 완료</li>
            <li class="active">송금 진행</li>
            <li>환전 완료</li>
          </ul>
        </div>
      </div>
      <div id="spinner" class="spinner-border text-success" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
  <!-- showDisplay 4  송금 진행 -->
  <div v-show="showDisplay===5">
    <div>
      <div id="showProgress">
        <div class="container">
          <ul class="progressbar">
            <li class="active">환전 신청</li>
            <li class="active">입금 대기</li>
            <li class="active">입금 완료</li>
            <li class="active">송금 진행</li>
            <li class="active">환전 완료</li>
          </ul>
        </div>
      </div>
      <div id="showMessage" span> 환전에 성공하였습니다. 다음을 눌러 환전결과를 확인하세요</span> </div>
      <img id="next_button2" v-on:click="showResult" src="../assets/next_button.png">
    </div>
  </div>
  <!-- showDisplay 5  환전완료 -->

  <div v-show="showDisplay===6">
    <div>
      <div id="showProgress">
        <div class="container">
          <ul class="progressbar">
            <li>환전 신청</li>
            <li>입금 대기</li>
            <li>입금 완료</li>
            <li>송금 진행</li>
            <li>환전 완료</li>
          </ul>
        </div>
      </div>
      <div id="errorMessage" span> {{errorInfoParam.errorMessage}}</span> </div>
      <img id="home_button" v-on:click="home" src="../assets/home_button.png">

    </div>
  </div>
  <!-- showDisplay 6  실패화면 -->

  <div v-show="showDisplay===7">
    <div>
      <table id="table1" class="table">
        <thead>
          <tr>
            <th scope="col">정보</th>
            <th scope="col">값</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              보낸 계좌 주소
            </td>
            <td>
              {{exchangeInfoParam.sendAccountAddress}}
            </td>
          </tr>
          <tr>
            <td>
              보낸 토큰 종류
            </td>
            <td>
              {{exchangeInfoParam.sendSelected}}
            </td>
          </tr>
          <tr>
            <td>
              보낸 토큰 양
            </td>
            <td>
              {{exchangeInfoParam.sendAmount}}
            </td>

          </tr>
          <tr>
            <td>
              수수료
            </td>
            <td>
              0.001 Ether
            </td>
          </tr>
        </tbody>

      </table>

      <table id="table2" class="table">
        <thead>
          <tr>
            <th scope="col">정보</th>
            <th scope="col">값</th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <td>
              받는 계좌 주소
            </td>
            <td>
              {{exchangeInfoParam.sendAccountAddress}}
            </td>
          </tr>
          <tr>
            <td>
              받은 토큰 종류
            </td>
            <td>
              {{exchangeInfoParam.receiveSelected}}
            </td>
          </tr>
          <tr>
            <td>
              받은 토큰 양
            </td>
            <td>
              {{exchangeInfoParam.receiveAmount}}
            </td>
          </tr>
        </tbody>

      </table>
      <img id="arrow_show" src="../assets/arrow_show.png">
      <img id="home_button" v-on:click="home" src="../assets/home_button.png">

    </div>
  </div>
  <!-- showDisplay 7  환전 결과 화면 -->


</div>
</template>

<script>
import Web3 from 'web3';
import store from '../store';

import {
  TokenExchange_address,
  TokenExchange_ABI,
  A_tokenAddress,
  B_tokenAddress,
  C_tokenAddress,
  ERC20_ABI,
  Token_ABI,
} from './Contract'


let show_decimal = 10 ** 8;
export default {
  name: 'Exchange',
  data() {
    return {
      sendSelected: "",
      receiveSelected: "",
      sendAmount: "",
      receiveAmount: "",
      sendAccountAddress: "",
      tokenRate: "",
      tokenIndex: "",
      showDisplay: 0,
      showTokenRate: {},
      approvePending: true,
      exchangePending: true,

    }
  },
  computed: {
    calculatedRate: function() {
      if (this.sendSelected === 'A_token' && this.receiveSelected === 'B_token') {
        this.tokenIndex = 0;
        return (this.sendAmount * this.tokenRate[0] / show_decimal)
      } else if (this.sendSelected === 'A_token' && this.receiveSelected === 'C_token') {
        this.tokenIndex = 1;
        return (this.sendAmount * this.tokenRate[1] / show_decimal)
      } else if (this.sendSelected === 'B_token' && this.receiveSelected === 'A_token') {
        this.tokenIndex = 2;
        return (this.sendAmount * this.tokenRate[2] / show_decimal)
      } else if (this.sendSelected === 'B_token' && this.receiveSelected === 'C_token') {
        this.tokenIndex = 3;
        return (this.sendAmount * this.tokenRate[3] / show_decimal)
      } else if (this.sendSelected === 'C_token' && this.receiveSelected === 'A_token') {
        this.tokenIndex = 4;
        return (this.sendAmount * this.tokenRate[4] / show_decimal)
      } else if (this.sendSelected === 'C_token' && this.receiveSelected === 'B_token') {
        this.tokenIndex = 5;
        return (this.sendAmount * this.tokenRate[5] / show_decimal)
      } else return (this.sendAmount * 1)
    },
    calculatedShowRate: function() {
      this.showTokenRate = {};
      if (this.sendSelected === 'A_token') {
        this.showTokenRate.B_token = this.tokenRate[0] / show_decimal;
        this.showTokenRate.C_token = this.tokenRate[1] / show_decimal;
        return this.showTokenRate;
      }
      if (this.sendSelected === 'B_token') {
        this.showTokenRate.A_token = this.tokenRate[2] / show_decimal;
        this.showTokenRate.C_token = this.tokenRate[3] / show_decimal;
        return this.showTokenRate;
      }
      if (this.sendSelected === 'C_token') {
        this.showTokenRate.A_token = this.tokenRate[4] / show_decimal;
        this.showTokenRate.B_token = this.tokenRate[5] / show_decimal;
        return this.showTokenRate;
      }
    },
    exchangeInfoParam: function() {
      return this.$store.state.exchangeInfo;
    },
    errorInfoParam: function() {
      return this.$store.state.errorInfo;
    }

  },
  mounted() {
    // const web3 = new Web3(Web3.givenProvider || 'http://localhost:8545:8546');

    const provider = new Web3.providers.WebsocketProvider(
    "wss://ropsten.infura.io/ws"
    );
    const web3Socket = new Web3(provider);

    // this.renderChart(this.data, this.options)

    this.watchSetRate()
  },
  methods: {

    watchSetRate: function() {


      let tokenExchangeContract = new web3.eth.Contract(TokenExchange_ABI, TokenExchange_address);

      tokenExchangeContract.events.ResultForsetRate({}, (error, event) => {
        if (!error) {
          console.log(event.returnValues._rate);
          this.tokenRate = event.returnValues._rate
        } else {
          alert("Smart Contract에서 환전비를 확인 할 수 없습니다. ")
          console.log("watch setRate error :", error);
        }
      });
      // this.tokenRate = [871498946, 481185086, 11474483, 423682844, 20782023, 23602560];
    },

    confirm: function(event) { // next button method
      if (this.sendAccountAddress && this.sendAmount &&
        this.calculatedRate && this.sendSelected && this.receiveSelected  ) {
        let check_sendAccountAddress = this.sendAccountAddress.substring(0, 2);
        if (check_sendAccountAddress != "0x") alert("올바른 주소 형식을 입력해주세요.")
        else if (this.sendAccountAddress.length != 42) {
          alert("올바른 주소 형식을 입력해주세요.")
        }
        else if (parseFloat(this.sendAmount) < 0.0001){
          alert("보내는 양을 0.0001 이상으로 입력하세요")
        } else {
          store.commit('pollExchangeInfo', {
            sendSelected: this.sendSelected,
            receiveSelected: this.receiveSelected,
            sendAmount: this.sendAmount,
            receiveAmount: this.calculatedRate,
            sendAccountAddress: this.sendAccountAddress,
            sendSelected: this.sendSelected,
            rateIndex: this.tokenRate[this.tokenIndex]
          });
          this.showDisplay = 1;
        }
      }
      else {
        alert("정보를 모두 입력하세요.")
      }
    }, // end of next
    exchange: function(event) {
      alert("메타마스크를 통해 환전을 진행하세요");
      this.showDisplay = 2;
      let selectedSendToken = this.$store.state.exchangeInfo.sendSelected;
      let selectedReceiveToken = this.$store.state.exchangeInfo.receiveSelected;
      let selectedSendAmount = this.$store.state.exchangeInfo.sendAmount;
      let selectedReceiveAmount = this.$store.state.exchangeInfo.receiveAmount;
      let selectedSendAccountAddress = this.$store.state.exchangeInfo.sendAccountAddress;
      let selectedSendTokenAddress;
      let selectedReceiveTokenAddress;


      if (selectedSendToken === 'A_token') selectedSendTokenAddress = A_tokenAddress;
      if (selectedSendToken === 'B_token') selectedSendTokenAddress = B_tokenAddress;
      if (selectedSendToken === 'C_token') selectedSendTokenAddress = C_tokenAddress;
      if (selectedReceiveToken === 'A_token') selectedReceiveTokenAddress = A_tokenAddress;
      if (selectedReceiveToken === 'B_token') selectedReceiveTokenAddress = B_tokenAddress;
      if (selectedReceiveToken === 'C_token') selectedReceiveTokenAddress = C_tokenAddress;

      let tokenContract = new web3.eth.Contract(Token_ABI, selectedSendTokenAddress);
      let tokenExchangeContract = new web3.eth.Contract(TokenExchange_ABI, TokenExchange_address);

      let sendAmount = web3.utils.toWei(selectedSendAmount.toString(), 'ether');
      let receiveAmount = web3.utils.toWei(selectedReceiveAmount.toString(), 'ether');

      function setApprove(callback) {
        return new Promise((resolve, reject)=> {
          tokenContract.methods.increaseApprove(TokenExchange_address, receiveAmount.toString()).send({
            from: selectedSendAccountAddress
          }, (error, result) => {
            if (!error) {

              alert("환전 승인이 성공하였습니다. 메타마스크를 통해 다음을 진행해주세요")
              resolve()
            } else {
              if (error.message.includes("User denied transaction signature")) {
                alert("환전 승인 거부하였습니다.")
                reject("환전 승인 거부하였습니다.")
              } else {
                console.log('환전 승인 실패');
                reject("환전 승인에 실패하였습니다. 메타마스크와 입력한 보낼 지갑 주소가 동일한지 확인하세요");
              }
            }
          }); //end of getBalance
        }); //end of Promise
      } //end of setApprove
      setApprove.call(this)
        .then(() => { //then

          function Exchanging(callback) {
            return new Promise((resolve, reject) => {
              tokenExchangeContract.methods.exchange(selectedSendAccountAddress, sendAmount.toString(), receiveAmount.toString(),
                selectedSendTokenAddress, selectedReceiveTokenAddress).send({
                  from: selectedSendAccountAddress,
                  value: web3.utils.toWei("0.001","ether")
                },
                (error, result) => {
                  if (!error) {
                    console.log("환전 성공");
                    resolve()
                  } else {
                    if (error.message.includes("User denied transaction signature")) {
                      alert("환전 진행이 거부되었습니다.")
                      reject("환전 진행을 거부하였습니다. 사용자 거부 또는 SmartContract 토큰 확인 필요");
                    } else {
                      console.log('환전 진행 실패 : ' + error);
                      reject("환전 진행에 실패하였습니다. 입력한 주소와 토큰량을 확인해주세요");
                    }
                  }
                }) //end of exchange
            }); //end of Promise
          } //end of Exchaning


          Exchanging()
            .then(() => {
              this.showDisplay = 4;
              console.log('환전 후 ');
              this.showDisplay = 5;
            })
            .catch((error) => {
              store.commit('pollErrorInfo', {
                errorMessage: '환전 진행 실패 : ' + error
              });
              console.log('환전 진행 실패 :' + error);
              store.commit('initializationExchangInfo');
              this.showDisplay = 6;
            })

        })
        .catch((error) => {
          store.commit('pollErrorInfo', {
            errorMessage: error
          });
          console.log('catch error : ' + error);
          store.commit('initializationExchangInfo');
          this.showDisplay = 6;
        });


    }, // end of exchange
    showResult: function(event) {
      this.showDisplay = 7;
    }, // complete exchange
    before: function(event) { // next button method
      if (this.showDisplay == 1) {
        this.showDisplay = this.showDisplay - 1;
        this.resetInputForm();
      } // 초기화 후 화면 이전으로
      else if (this.showDisplay == 0) {
        store.commit('initializationExchangInfo');
        this.showDisplay = 0;
        this.resetInputForm();
        this.$router.push('/');
      }
    },
    home: function(event) {
      store.commit('initializationExchangInfo');
      store.commit('initializationErrorInfo')
      this.showDisplay = 0;
      this.resetInputForm();
    },
    resetInputForm: function() {
      this.sendAccountAddress = '';
      this.sendAmount = '';
    }
  } //end of methods
} //end of export
</script>


<style>
